import React, { Component } from 'react'

import './index.css'
import { meida_url } from '../../utils/utils'
import default_avatar from '../../assets/imgs/default_avatar.jpg'
import { updateUser, getUser, uploadImg } from '../../servers/index'
import Header from '../../components/Header'
import {message} from 'antd'

export default class ChangeInfo extends Component {
    constructor() {
        super()
        this.state = {
            avatar: "",
            nickname: '',
            sign: "",
            info: "",
        }
    }
    componentDidMount() {
        this.getUserInfo()
    }
    getUserInfo() {
        const username = this.props.match.params.username
        getUser({ username }).then(res => {
            if (res.code === 200) {
                const { nickname, sign, info, avatar } = res.data
                this.setState({ nickname, sign, info, avatar })
            } else {
                alert(res.error)
            }

        })
    }

    updateUserInfo() {
        const username = this.props.match.params.username
        const { nickname, sign, info } = this.state
        updateUser({ nickname, sign, info ,username}).then(res => {
            if (res.code === 200) {
                message.success('修改成功')
            } else {
                message.error(res.error)
            }
        })
    }

    //上传图片
    uploadPic() {
        const username = this.props.match.params.username
        let file = this.fileInput.files[0]
        let formData = new FormData();
        formData.append('avatar', file)
        uploadImg({ username, avatar: formData }).then(res => {

            if (res.code === 200) {
                const avatar = res.data.avatar
                this.setState({ avatar })
                this.header.changeAvatar(res.data.avatar)
                let userInfo = localStorage.getItem('userInfo')
                userInfo = JSON.parse(userInfo)
                userInfo.avatar = avatar
                localStorage.setItem('userInfo',JSON.stringify(userInfo))
                message.success('上传成功')
            } else {
                message.error(res.error)
            }

        })
    }

    previewImg(e) {

    }
    render() {
        const { avatar, nickname, sign, info } = this.state
        return (
            <div className="change-info-wrapper">
                <Header {...this.props} ref={(header)=>this.header = header}/>
                <div className="card col-md-6 change-info">
                   
                    <h4>个人信息修改</h4>
                   
                    <div className="avatar-wrapper">
                        <form encType="multipart/form-data">
                            <div >
                                <img src={avatar ? meida_url + avatar : default_avatar} alt="" className="userinfo-avatar" />
                            </div>
                            <div className="upload-wrapper">
                                <label htmlFor="avatar">上传头像:</label>
                                <input type="file" name="avatar" id="avatar" style={{color:'#fff'}}
                                 ref={(fileInput) => this.fileInput = fileInput} onChange={this.previewImg.bind(this)} />
                            </div>
                            {/* <button id="submit-btn" className="btn primary" >上传</button> */}
                            <button type="button" className="btn btn-primary" onClick={this.uploadPic.bind(this)}>上传</button>
                        </form>
                    </div>
                    <div className="info-input-wrapper">
                        <div className="info-input-item">
                            <label >昵称: </label>
                            <input type="text" className="nickname form" placeholder="请输入新的昵称" value={nickname} onChange={(e) => this.setState({ nickname: e.target.value })} />
                        </div>
                        <div className="info-input-item">
                            <label >个人签名: </label>
                            <input type="text" className="sign" placeholder="请输入个人签名" value={sign} onChange={(e) => this.setState({ sign: e.target.value })} />
                        </div>
                        <div className="info-input-item">
                            <textarea className="info" placeholder="请输入个人描述" value={info} onChange={(e) => this.setState({ info: e.target.value })}></textarea>
                        </div>
                        <div style={{paddingBottom: '30px'}}>
                            <button type="button" value="保存" className="btn btn-primary" onClick={this.updateUserInfo.bind(this)}>
                                保存
                        </button>
                        </div>

                    </div>

                </div>
            </div>

        )
    }
}